@import url(https://fonts.googleapis.com/css2?family=Cute+Font&display=swap);
.wrapper{
    margin-top: 5%;
    width: 50%;
    padding: 2rem;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    grid-gap: 2rem;
    gap: 2rem;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-evenly;
            justify-content: space-evenly;
    background: rgba(0, 0, 0, 0.308);
}
.card{
    height: 30rem;
    width: 20rem;
    border-radius: 5px;
    cursor: pointer;
}
.card:hover{
    scale: 1.2;
}

@media only screen 
  and (min-device-width: 320px) 
  and (max-device-width: 480px)
  and (-webkit-min-device-pixel-ratio: 2) {

    .wrapper{
        margin-top: 5%;
        width: 100%;
        padding: 2rem;
        -webkit-flex-wrap: wrap;
                flex-wrap: wrap;
        grid-gap: 3rem;
        gap: 3rem;
        display: -webkit-flex;
        display: flex;
        -webkit-justify-content: space-evenly;
                justify-content: space-evenly;
        background: rgba(0, 0, 0, 0.308);
    }
    .card{
        height: 10rem;
        width: 6.5rem;
        border-radius: 5px;
    }
    .card:hover{
        scale: 1.2;
    }
}
.scoreContainer{
    display: -webkit-flex;
